import { AxiosResponse, AxiosPromise } from 'axios'
import Vue from 'vue'
import identity from 'lodash/identity'

/**
 * 
 * @param res []
 * @param options []
 */
export const handleRes = (res, options: { errorMsg?: string, successMsg?: string } = {}) => {
  let status: boolean
  if (res.code === -1) {
    Vue.$toast.error(' code: ' + res.code)
    status = false
  }
  else if (res.code === 1) {
    Vue.$toast.error(options.errorMsg || res.msg || (' code: ' + res.code))
    status = false
  } else {
    Vue.$toast.success(options.successMsg || res.msg || '')
    status = true
  }
  return status
}

/**
 * 
 * @export
 * @param {AxiosResponse} response 
 * @returns 
 */
export function checkStatus(response: AxiosResponse) {
  if (response.status >= 200 && response.status < 300) {
    return response
  } else { // todo 
    let error = new Error(response.statusText)
    error['response'] = response
    throw error
  }
}

export function parseJson(response: AxiosResponse) {
  return response.data
}

/**
 * 
 * 
 * @export
 * @param {Function} task  
 * @param {{params?:Object,data?:Object}} body [paramsapidata]
 * @param {Function} cb 
 * @param {Function} cb2 
 */
export function fetchItem(task: { (reqUrl: UrlConfig): AxiosPromise }, body: UrlConfig = {}, cb?: Function, cb2?: Function) {
  return task(body)
    .then(checkStatus)
    .then(parseJson)
    .then(json => {
      cb && cb(json)
      return json
    })
    .catch(e => {
      Vue.$toast.error(e && e.message)
      cb2 && cb2()
    })
}

